import React, { Component } from 'react'
import '../App.css'

class Body extends Component {
    render() {
        return (
            <div className="p">
                <div>
                    Preparing for interviews?
                    Scubscribe to this newsletter and
                    get daily practice problems to get <b>One Step Closer</b>
                </div>
                <form>
                    <label>
                        Email:
                        <input type="text" name="name" placeholder="Enter your Email Id..." />
                    </label>
                    <br />
                    <button onClick={this.handleClick}>Subscribe Now!</button>
                </form>
            </div>
        )
    }
}

export default Body;